<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增标杆与定额</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm">
        <el-form-item label="产品名称">
          <!-- <el-input v-model="searchForm.formdata.entEnergyId"></el-input> -->
          <el-select v-model="searchForm.productId" filterable placeholder="请选择">
            <el-option v-for="item in productIdOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="能源种类">
          <el-select v-model="searchForm.energyId" filterable placeholder="请选择">
            <el-option v-for="item in energyIdOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="指标类型">
          <da-select-tree :data="targetTypeOptions" v-model="searchForm.objectType" :defaultExpandAll="true"></da-select-tree>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>

      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="210" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="getTransfer('benchmarkRelation',scope.row)">关联指标</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">
        <el-form-item label="标杆名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="关联指标类型" prop="targetType">
          <da-select-tree :data="targetTypeOptions" v-model="addForm.targetType" :defaultExpandAll="true"></da-select-tree>
        </el-form-item>
        <el-form-item label="计量单位" prop="unit">
          <el-input auto-complete="off" v-model="addForm.unit"></el-input>
        </el-form-item>
        <el-form-item label="时间类型" prop="timeFlag">
          <el-select v-model="addForm.timeFlag">
            <el-option label="全部" :value="0"></el-option>
            <el-option label="日" :value="3"></el-option>
            <el-option label="月" :value="5"></el-option>
            <el-option label="年" :value="8"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关联产品" prop="productId">
          <el-select v-model="addForm.productId" filterable placeholder="请选择">
            <el-option v-for="item in productIdOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关联能源类型" prop="energyId">
          <el-select v-model="addForm.energyId" filterable placeholder="请选择">
            <el-option v-for="item in energyIdOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>

    <!-- 关联关系 -->
    <da-dialog title="关联指标" :visible.sync="benchmarkRelationDialogVisible" size="700px">
      <relev-transfer ref="benchmarkRelation" :visible.sync="benchmarkRelationDialogVisible" :params-attr="benchmarkId" :base-attr="meterAttr"></relev-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveTransferDialog('benchmarkRelation')">确 定</el-button>
        <el-button @click="benchmarkRelationDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>

    <da-manage-page>
      <div class="pad-tb-10" slot="button">
        <el-button icon="my-zengjia" @click="benchLimitDataDialogVisible=true;benchLimitDataAddDataType=2;">新增定额值</el-button>
        <el-button icon="my-zengjia" @click="benchLimitDataDialogVisible=true;benchLimitDataAddDataType=3;">新增目标值</el-button>
        <el-button icon="my-zengjia" @click="benchLimitDataDialogVisible=true;benchLimitDataAddDataType=1;">新增标杆值</el-button>
        <el-button icon="my-zengjia" @click="benchmarkTypeDialogVisible=true;">新增标杆类型</el-button>
        <el-button icon="my-zengjia" @click="benchmarkTypeDialogVisible=true;">删除标杆类型</el-button>
      </div>

      <da-table ref="benchLimitDataTable" slot="content" :colums="colsBenchLimitData" :table-list="benchLimitDataList" @callBack="getBenchLimitDataList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editBenchLimitData(scope.row)">编辑</el-button>
            <el-button size="small" @click="delBenchLimitData(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="benchLimitDataTitle" :visible.sync="benchLimitDataDialogVisible" @close="closeBenchLimitDataDialog('benchLimitDataAddForm')">
      <el-form label-width="120px" :model="benchLimitDataAddForm" :rules="benchLimitDataRules" ref="benchLimitDataAddForm">
        <el-form-item label="数据类型" prop="dataType">
          <el-input auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="数值" prop="data">
          <el-input auto-complete="off" v-model="benchLimitDataAddForm.data"></el-input>
        </el-form-item>
        <el-form-item label="生效时间" prop="startTime">
          <el-date-picker v-model="benchLimitDataAddForm.startTime" type="date" placeholder="选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="截止时间" prop="endTime">
          <el-input auto-complete="off" v-model="benchLimitDataAddForm.endTime"></el-input>
        </el-form-item>
        <el-form-item label="修改人" prop="operationUserName">
          <el-input auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveBenchLimitDataDialog('benchLimitDataAddForm')">确 定</el-button>
        <el-button @click="benchLimitDataDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>

    <da-dialog title="新增标杆类型" :visible.sync="benchmarkTypeDialogVisible" @close="closeBenchmarkTypeDialog('benchmarkTypeAddForm')">
      <el-form label-width="120px" :model="benchmarkTypeAddForm" :rules="benchmarkTypeRules" ref="benchmarkTypeAddForm">
        <el-form-item label="新增标杆类型" prop="name">
          <el-input auto-complete="off" v-model="benchmarkTypeAddForm.name"></el-input>
        </el-form-item>
        <el-form-item label="已定义标杆类型" prop="">
          <el-input auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="常用标杆类型" prop="">
          <el-input auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="自定义标杆类型" prop="">
          <el-input auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveBenchmarkTypeDialog('benchmarkTypeAddForm')">保 存</el-button>
        <el-button @click="autoBenchmarkTypeDialog('benchmarkTypeAddForm',1)">自动生成常用标杆</el-button>
        <el-button @click="autoBenchmarkTypeDialog('benchmarkTypeAddForm',0)">自动生成自定义标杆</el-button>
        <el-button @click="benchmarkTypeDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>

    <!-- <da-dialog title="删除标杆类型" :visible.sync="benchmarkTypeDelDialogVisible" @close="closeBenchmarkTypeDelDialog('benchmarkTypeAddForm')">
      <el-form label-width="120px" :model="benchmarkTypeAddForm" :rules="benchmarkTypeRules" ref="benchmarkTypeAddForm">
        <el-form-item label="删除标杆类型" prop="id">
          <el-input auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveBenchmarkTypeDialog('benchmarkTypeAddForm')">删 除</el-button>
        <el-button @click="benchmarkTypeDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog> -->

  </div>
</template>
<script>
import { changeTree } from "../../../util/util";
import relevTransfer from "../basic/template/relevProduct";
export default {
  name: "calorificEnergy",
  components: { relevTransfer },
  data() {
    return {
      productIdOptions: [],
      energyIdOptions: [],
      targetTypeOptions: [],

      chooseBenchlimitId: "", // 选择的指标标杆定额

      // 查询表单
      searchForm: {
        productId: "",
        energyId: "",
        type: 1,
        objectType: ""
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "名称", prop: "name", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: {
        name: "",
        type: "1",
        targetType: "",
        energyId: "",
        productId: "",
        unit: "",
        timeFlag: ""
      },
      rules: this.eachValidate({
        name: { required: true, maxString: "50" },
        targetType: { required: true },
        energyId: {},
        productId: {},
        unit: { required: true, maxString: "20" },
        timeFlag: { required: true }
      }),

      benchmarkId: "",
      benchmarkRelationDialogVisible: false,
      meterAttr: {
        dataUrl: "rest/benchmarkQuotaTarget/relation", // 获取数据的地址
        getPramsAttr: "benchmarkId", // 获取数据的参数属性名
        saveUrl: "rest/benchmarkQuotaTarget/relation", // 保存数据的地址
        valueKey: "id",
        label: "name",
        titles: ["未关联指标", "已关联指标"]
      },
      // 标杆数据查询表单
      benchLimitDataSearchForm: {
        benchlimitId: 1,
        currentPage: 1,
        pageSize: 999
      },
      // 标杆数据列表
      benchLimitDataList: {},
      // 标杆数据表头
      colsBenchLimitData: [
        {
          label: "数据类型",
          prop: "dataType",
          align: "left",
          displace: {
            1: "标杆",
            2: "定额",
            3: "目标"
          },
          sortable: true
        },
        { label: "数值", prop: "data", align: "left", sortable: true },
        {
          label: "生效起始时间",
          prop: "startTime",
          align: "left",
          format: "YYYY-MM-DD",
          sortable: true
        },
        {
          label: "数据录入人",
          prop: "operationUserName",
          align: "left",
          sortable: true
        },
        {
          label: "数据录入时间",
          prop: "operationTime",
          format: "YYYY-MM-DD HH:mm:ss",
          align: "left",
          sortable: true
        }
      ],
      benchLimitDataDialogVisible: false,
      benchLimitDataAddDataType: "",
      benchLimitDataAddForm: {
        benchlimitId: "",
        dataType: "",
        benchlimitSubdivideId: "",
        data: "",
        rate: "1",
        startTime: "",
        endTime: "",
        operationTime: "",
        operationUser: "",
        remark: ""
      },
      benchLimitDataRules: this.eachValidate({
        benchlimitId: {},
        dataType: {},
        benchlimitSubdivideId: {},
        data: { required: true },
        rate: {},
        startTime: { required: true },
        endTime: {},
        operationTime: {},
        operationUser: {},
        remark: {}
      }),
      // 标杆类型
      benchmarkTypeDialogVisible: false,
      benchmarkTypeAddForm: {
        benchlimitId: "",
        name: ""
      },
      benchmarkTypeRules: this.eachValidate({
        benchlimitId: {},
        name: { required: true, maxString: "50" }
      }),
      benchmarkCommonType: [], // 常用标杆类型
      benchmarkCustomType: [], // 自定义标杆类型
      benchmarkTypeList: [] // 已定义的标杆类型
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑标杆定额目标" : "新增标杆定额目标";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    },
    benchLimitDataTitle() {
      let str = this.benchLimitDataAddForm.id ? "编辑" : "新增";
      if (
        this.benchLimitDataAddForm.dataType == 1 ||
        this.benchLimitDataAddDataType == 1
      ) {
        str += "标杆值";
      } else if (
        this.benchLimitDataAddForm.dataType == 2 ||
        this.benchLimitDataAddDataType == 2
      ) {
        str += "定额值";
      } else {
        str += "目标值";
      }
      //弹窗标题
      return str;
    }, //"新增标杆类型"
    benchLimitDataUrlType() {
      //URL请求方式
      return this.benchLimitDataAddForm.id ? "put" : "post";
    },
    benchLimitDataTitle() {
      return "";
    }
  },
  watch: {},
  methods: {
    getProductIdOption() {
      let formName = "table";
      this.$ajax
        .get("rest/benchmarkQuotaTarget/product", {
          params: { type: 1 }
        })
        .then(res => {
          if (res.data.status) {
            this.productIdOptions = res.data.records;
          }
        });
    },
    getEnergyIdOption() {
      let formName = "table";
      this.$ajax.get("rest/entEnergy/useEnergy").then(res => {
        if (res.data.status) {
          this.energyIdOptions = res.data.records;
        }
      });
    },
    getTargetTypeOption(val) {
      let formName = "table";
      this.$ajax.get("rest/targetData/targetTypeTree").then(res => {
        if (res.data.status) {
          this.targetTypeOptions = res.data.records;
          //编辑设值
          if (val != undefined) {
            //没有parentname时
            let parentTreeObj = {};
            changeTree(res.data.records, "treeCode", "name", parentTreeObj);
            // this.$refs.parentModuleTree.setText(parentTreeObj[val.parentId]);
          }
        }
      });
    },
    getTransfer(refName, row) {
      this.benchmarkId = row.id;
      this[refName + "DialogVisible"] = true;
      this.$nextTick(() => {
        this.$refs[refName].getData(row);
      });
    },
    saveTransferDialog(refName) {
      this.$refs[refName].saveData();
    },
    getTbList() {
      let formName = "table";
      let paramObj = this.searchForm;
      paramObj.type = 1;
      this.$ajax
        .get("rest/benchmarkQuotaTarget/benchmarkQuota", {
          params: paramObj,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          entEnergyId: "",
          dengValue: "",
          dangValue: "",
          displayUnit: "",
          dengModulus: "",
          dangModulus: "",
          effectiveTime: ""
        };
      });
    },
    saveDialog(formName) {
      let paramObj = this.addForm;
      paramObj.type = 1;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType](
            "rest/benchmarkQuotaTarget/benchmarkQuota",
            paramObj
          ).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/benchmarkQuotaTarget/benchmarkQuota", {
            params: { id: row.id }
          })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    },
    // 查询标杆定额目标数据
    getBenchLimitDataList() {
      let formName = "benchLimitDataTable";
      let paramObj = this.benchLimitDataSearchForm;
      if (this.chooseBenchlimitId) {
        paramObj.benchlimitId = this.chooseBenchlimitId;
      }
      this.$ajax
        .get("rest/benchmarkQuotaTarget/benchLimitData", {
          params: paramObj,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.benchLimitDataList = res.data;
          }
        });
    },
    // 新增标杆定额目标数据
    // 编辑标杆定额目标数据
    editBenchLimitData(row) {
      this.benchLimitDataDialogVisible = true;
      this.benchLimitDataAddForm = JSON.parse(JSON.stringify(row));
    },
    closeBenchLimitDataDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.benchLimitDataAddForm = {
          benchlimitId: "",
          dataType: "",
          benchlimitSubdivideId: "",
          data: "",
          rate: "1",
          startTime: "",
          endTime: "",
          operationTime: "",
          operationUser: "",
          remark: ""
        };
      });
    },
    // 保存标杆定额目标数据
    saveBenchLimitDataDialog(formName) {
      let paramObj = this.benchLimitDataAddForm;
      if (this.chooseBenchlimitId) {
        paramObj.benchlimitId = this.chooseBenchlimitId;
      } else {
        paramObj.benchlimitId = 1;
      }
      paramObj.dataType = this.benchLimitDataAddDataType;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.benchLimitDataUrlType](
            "rest/benchmarkQuotaTarget/benchLimitData",
            paramObj
          ).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.benchLimitDataDialogVisible = false;
              this.getBenchLimitDataList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    // 删除标杆定额目标数据
    delBenchLimitData(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/benchmarkQuotaTarget/benchLimitData", {
            params: { id: row.id }
          })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    },

    // 标杆类型
    // 保存
    saveBenchmarkTypeDialog(formName) {
      let paramObj = this.benchmarkTypeAddForm;
      if (this.chooseBenchlimitId) {
        paramObj.benchlimitId = this.chooseBenchlimitId;
      } else {
        paramObj.benchlimitId = 1;
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax["post"](
            "rest/benchmarkQuotaTarget/benchmarkType",
            paramObj
          ).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.benchmarkTypDialogVisible = false;
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    // 关闭弹窗
    closeBenchmarkTypeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.benchmarkTypeAddForm = {
          benchlimitId: "",
          name: ""
        };
      });
    },
    // 自动生成
    autoBenchmarkTypeDialog(formName, type) {
      let paramObj = {};
      if (this.chooseBenchlimitId) {
        paramObj.benchlimitId = this.chooseBenchlimitId;
      } else {
        paramObj.benchlimitId = 1;
      }
      paramObj.type = type;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax["post"](
            "rest/benchmarkQuotaTarget/autoBenchmarkType",
            paramObj
          ).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.benchmarkTypDialogVisible = false;
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    // 查询常用标杆类型和自定义标杆类型
    getBenchmarkCommonType() {
      this.$ajax.get("rest/benchmarkQuotaTarget/benchLimitData").then(res => {
        if (res.data.status) {
          res.data.forEach(element => {
            if (element.type == 0) {
              benchmarkCustomType.push(element);
            } else {
              benchmarkCommonType.push(element);
            }
          });
        }
      });
    },
    // 查询已定义的标杆类型
    getBenchmarkTypeList() {
      let paramObj = {};
      if (this.chooseBenchlimitId) {
        paramObj.benchlimitId = this.chooseBenchlimitId;
      } else {
        paramObj.benchlimitId = 1;
      }
      this.$ajax
        .get("rest/benchmarkQuotaTarget/benchmarkType", {
          params: paramObj
        })
        .then(res => {
          if (res.data.status) {
            this.benchmarkTypeList = res.data;
          }
        });
    },
    // 删除
    delBenchmarkTypeList() {
      let ids = "";
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/benchmarkQuotaTarget/benchmarkType", {
            params: { ids: ids }
          })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getTbList();
    this.getProductIdOption();
    this.getEnergyIdOption();
    this.getTargetTypeOption();
    this.getBenchLimitDataList();
  }
};
</script>



